.wrapper {
  margin: 0.1rem 0;
}

.status {
  padding: 0 0.5rem;
  margin-left: -0.5rem;
  font-size: 0.9rem;
  line-height: 1.9;
  border: 1px solid transparent;
}

.statusHover {
  cursor: pointer;

  &:hover {
    border: 1px solid #f5f5f5;
    background-color: #f5f5f5;
  }
}

.editor {
  width: 100%;
  padding: 0 0.5rem;
  margin-left: -0.5rem;
  font-size: 0.9rem;
  line-height: 1.9;
  border-radius: 2px;
  border: 1px solid #e1e1e1;

  &:focus {
    outline: none;
  }
}