@import "../../../assets/styles/data";

.cover {
  position: relative;
  height: 15rem;
  overflow: hidden;
  margin-bottom: 1rem;

  img {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }
}

.info {
  margin-bottom: 2rem;
  display: flex;
  align-items: flex-start;

  h3 {
    margin-bottom: 0;
  }
}

.avatar {
  width: 10rem;
}

.desc {
  position: relative;
  z-index: 1;
  flex: 1 1 auto;
  padding: 1rem;
  background: #ffffff;

  &About {
    margin-bottom: 0.5rem;
  }
}

.name {
  font-size: 2rem;
  line-height: 0.8;
}

.data {

}

.contacts {
  position: relative;
  margin-top: 1rem;
  font-size: 0.9rem;

  &__item {
    display: block;
    font-style: italic;
  }
}

.editLink {
  position: absolute;
  top: 1rem;
  right: 1rem;
  padding: 0;
  border: 0;
  color: $main-color;
  cursor: pointer;
  background: none;
  outline: 0;

  &:hover {
    text-decoration: underline;
  }
}

.editForm {
  position: relative;
  z-index: 1;
  width: 100%;
  padding-left: 1rem;
  background: #ffffff;
}